@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Roboto&family=Special+Elite&display=swap);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg-color:linear-gradient(to bottom left, #dbc4c4, #e7e73f, green);
  /* --bg-color: linear-gradient(to right bottom, #532623, #975420, #bb9900, #a8eb12); */
 

   --light-text: white;
   --light-text-2: #e04242;
   --light-gray:lightgray;

   /* white */
   --white:#ffffff;
   --white-1:#f5f5f5;
   --white-2:#ebebeb;
   --white-3:#e0e0e0;
   --white-4:#d6d6d6;
   --white-5:#cccccc;

   --black:black;
 
   --bg-color-1:#0b520b;
   --bg-color-2:#333333e6;
   --bg-color-3:#becf21;
   --bg-color-4:#0b213b80;
   --bg-color-5:#0b520b;
   --bg-color-6:#8b9818;
 
    --color-1:#7290be;
    --color-2:#cecece;
    --color-3:#f5f5f5;
    --color-4:#333;
 
   --font-1: 'Caveat', cursive;
   --font-2:'Roboto', sans-serif;
   --font-3:'Special Elite', cursive;
 
   --ancient-1:#e9d8a6;
   --ancient-2:#ee9b00;
   --ancient-3:#94d2bd;
   --ancient-4:#0a9396;
   --ancient-4:#005f73;

   /* bluish */
   --neutral-0:#48cae4;
   --neutral-1:#00b4d8;
   --neutral-2:#0096c7;
   --neutral-3:#0077b6;
   --neutral-4:#023e8a;
   --neutral-5:#03045e;

    /* burlywood */
    --br-1:#ede0d4;
    --br-2:#e6ccb2;
    --br-3:#ddb892;
    --br-4:#b08968;
    --br-5:#7f5539;
    --br-6:#9c6644;

    /* red */
    --rd-1:#e85d08;
    --rd-2:#dc2f02;
    --rd-3:#d00000;
    --rd-4:#9d0208;
    --rd-5:#6a040f;
    --rd-6:#370617;

    /* green */
    --gr-1:#004b23;
    --gr-2:#006400;
    --gr-3:#007200;
    --gr-4:#008000;
    --gr-5:#38b000;
    --gr-6:#70e000;

    /* melon */
    --ml-1:#e8a598;
    --ml-2:#ffb5a7;
    --ml-3:#fec5bb;
    --ml-4:#fcd5ce;
    --ml-5:#fae1dd;
    --ml-6:#f8edeb;

    /* yellow */
    --yellow-1:#ffea00;
    --yellow-2:#ffdd00;
    --yellow-3:#ffd000;
    --yellow-4:#ffc300;
    --yellow-5:#ffb700;
    --yellow-6:#ffaa00;

}

body{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to bottom left, #dbc4c4, #e7e73f, green);
  background-image: var(--bg-color);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  text-align: center;
 
}

/* ####### Button ###### */
button {
  font-family: "Special Elite", cursive;
  border: none;
  background-color: #ffffff;
  background-color: var(--white);
  font-family: 'Special Elite', cursive;
  font-family: var(--font-3);
  padding: 1rem 1rem;
  margin: 20px;
  box-shadow: 0 0 5px #333;
  box-shadow: 0 0 5px var(--color-4);
  border-radius: 5px;
  text-transform: uppercase;
  font-size: 20px;
  cursor: pointer;

}

h1, p{
  padding: 1.5rem;
  font-family: 'Special Elite', cursive;
  font-family: var(--font-3);
}
h1{
  font-size: 3rem;
}

p{
  font-size: 3rem;
}

/* ####### Header ###### */
header{
  padding: 15px;
  text-align: center;
  background-color: #0b213b80;
  background-color: var(--bg-color-4);
  color: black;
  color: var(--black);
  width: 100%;
  font-size: 70px;
  text-transform: uppercase;
  font-family: 'Special Elite', cursive;
  font-family: var(--font-3);
}

.app h1::before{
  content: "";
  background-color:ðŸ¤› ;
}

/* ####### Footer ###### */
footer{
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-2);
  width: 100%;
  position: absolute;
  bottom: 0;
  text-align: center;
  color: black;
  color: var(--black);
  background-color: #becf21;
  background-color: var(--bg-color-3);
}

.footerWrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

footer p{ 
  font-family: 'Roboto', sans-serif; 
  font-family: var(--font-2);
  font-size: 1.5rem;
  padding: 30px;
}

footer .icons i {
  margin: 20px ;
  font-size: 50px;
  color: #8b9818;
  color: var(--bg-color-6);
}

footer .icons i:hover{
  color: #ee9b00;
  color: var(--ancient-2);
  transition: 1s;
}

/* ------############# Mobile size iphone 5/SE #############-----------*/

@media screen and (min-width:320px) and (max-width:568px){
  header{
    padding: 20px;
    width: 100%;
    font-size: 40px;
  }
  footer{
    bottom: 0;
    width: 100%;
    position: relative;
  }

  h1{
    padding: 5px;
    font-size: 15px;
  }

  p{
    font-size: 1rem;
  }

  button {
    padding: 0.3rem 0.5rem;
    margin: 10px;
  }
}

/* ------############# Ipad #############-----------*/
@media screen and (min-width:769px) and (max-width:1024px) {
  h1{
    padding: 15px;
    font-size: .8rem;
  }

  p{
    padding: 2rem;
    font-size: 2rem;
  }
  button {
    padding: 0.5rem 0.5rem;
    margin: 10px;
  }
  footer{
    bottom: 0;
    width: 100%;
  }

}

@media screen and (min-width:769px) and (max-width:1024px) {
  footer{
      position: absolute;
  }

}

.circle{
    color: var(--white);
    width: 100px;
    height: 100px;
    margin-bottom: 40px;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
    box-shadow: 0 0 10px var(--ancient-2);
}

.circle:active{
    box-shadow: inset 0 0 40px var(--black);
    transform: scale(1.5);
}

.green{
    background-color: var(--neutral-1);
}
.blue{
    background-color: var(--color-1);
}
.red{
    background-color: var(--ancient-2);
}

.gray{
    background-color: var(--neutral-4);
}

.pink{
    background-color: var(--neutral-5);
}


.active{
    box-shadow: 0 0 25px var(--ancient-2);
    border: 25px solid var(--white);
    background-image: url(data:image/png;base64,);
    background-size: 100%;
    cursor:url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'  width='93' height='112' viewport='0 0 100 100' style='fill:black;font-size:56px;'><text y='50%'>ðŸ‘†</text></svg>") 16 0,auto;
}

/* ------############# Mobile size iphone 5/SE #############-----------*/

@media screen and (min-width:320px) and (max-width:568px){
    .circle{
        width: 50px;
        height: 50px;
        margin: 5px;
       
    }

    .active{
        box-shadow: 0 0 25px var(--ancient-2);
        border: 25px solid var(--white);
        background-image: url(data:image/png;base64,);
        background-size: 100%;
        cursor:url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'  width='93' height='112' viewport='0 0 100 100' style='fill:black;font-size:56px;'><text y='50%'>ðŸ‘†</text></svg>") 16 0,auto;
    }

    
}

/* ------############# Ipad Pro #############-----------*/
@media  screen and (min-width: 540px) and (max-width: 768px){
    .circle{
        width: 70px;
        height: 70px;
        margin: 20px;
       
    }
}

@media screen and (min-width:769px) and (max-width:1024px) {
    footer{
        position: absolute;
    }

    .circle{
        width: 70px;
        height: 70px;
        margin: 20px;
       
    }

  
}

.overlay button{
  background-color: var(--bg-color-1);
  color:var(--white);
  border: none;
  border-radius: 5px;
  padding:0.6rem;
  margin:0 auto;
  font-size: 30px;
  top: 3px;
  right: 10px;
  position: absolute;
  text-align: center;   
}

.popup{
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: var(--bg-color-2);
  display: flex;
  justify-content: center;
  align-items: center;
}  
  
.overlay{
  background-color: var(--bg-color-3);
  color:var(--black);
  padding: 20px;
  width:80%;
  height: 70%;
  position: relative;
  top:0;
  left:0;
  border-radius: 10px;
} 
  

/* ------############# mobile #############-----------*/


@media (max-width: 568px) {
  .overlay{
  padding: 20px;
  width:60%;
  height: 60%;
  }

  .overlay button{
    padding:0.6rem;
    font-size: 10px;
    top: 3px;
    right: 10px; 
  }

  footer{
    position: fixed;
}
}

/* ------############# Ipad Pro #############-----------*/
@media  screen and (min-width: 540px) and (max-width: 768px) {
